@import url(../../node_modules/modern-normalize/modern-normalize.css);
:global(.alignStart) {
  text-align: left; }
  :global(.rtl) :global(.alignStart) {
    text-align: right; }

:global(.alignEnd) {
  text-align: right; }
  :global(.rtl) :global(.alignEnd) {
    text-align: left; }

:global(.offscreen) {
  position: absolute;
  top: -9999px;
  left: -9999px; }

:global(.hspaceStart) {
  display: flex;
  flex-flow: row wrap;
  margin-top: -0.8rem;
  margin-left: -0.8rem; }
  :global(.hspaceStart) > * {
    margin-top: 0.8rem;
    margin-left: 0.8rem; }
  :global(.rtl) :global(.hspaceStart) {
    flex-flow: row-reverse wrap; }

:global(.hspaceEnd) {
  display: flex;
  flex-flow: row wrap;
  margin-top: -0.8rem;
  margin-left: -0.8rem;
  justify-content: flex-end; }
  :global(.hspaceEnd) > * {
    margin-top: 0.8rem;
    margin-left: 0.8rem; }
  :global(.rtl) :global(.hspaceEnd) {
    flex-flow: row-reverse wrap; }

:global(.vspaceStart) {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: -0.8rem; }
  :global(.vspaceStart) > * {
    margin-bottom: 0.8rem; }

:global(.vspaceEnd) {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: -0.8rem;
  justify-content: flex-end; }
  :global(.vspaceEnd) > * {
    margin-bottom: 0.8rem; }

:global(.draggable) {
  cursor: all-scroll; }

:global(.wysiwyg) {
  color: #333; }
  :global(.wysiwyg) p {
    margin: 0 0 0.8rem;
    font-size: 1.4rem; }
  :global(.wysiwyg) :last-child {
    margin-bottom: 0; }

html, body {
  font-family: sans-serif;
  font-size: 10px;
  height: 100%; }

:global(#app) {
  height: 100%; }

:focus {
  outline: 0; }
