.modal {
  position: absolute;
  z-index: 100;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

.content {
  position: absolute;
  z-index: 2;
  top: 6rem;
  left: 1.6rem;
  right: 1.6rem;
  max-width: 80rem;
  width: calc(100% - 3.2rem);
  min-width: 28.8rem;
  max-height: calc(100vh - 12rem);
  margin: 0 auto;
  border: 0.1rem solid #000;
  border-bottom: 0;
  background-color: #FFF;
  outline: none;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0.5rem 0 0 #000; }
  :global(.isIE) .content {
    height: 100%; }
  .content.mode-error {
    box-shadow: 0 0.5rem 0 0 #EE3333; }
  .content.mode-success {
    box-shadow: 0 0.5rem 0 0 #33EE33; }
  .content.size-thin {
    max-width: 42rem; }
  .content.size-lessThin {
    max-width: 52rem; }
  .content.size-wide {
    max-width: 130rem; }

.section {
  display: flex;
  flex-direction: column; }

.header {
  flex: 0 0 auto;
  padding: 0.8rem 3.2rem 0.8rem 0.8rem;
  font-size: 2.1rem;
  font-weight: bold;
  line-height: 1;
  position: relative; }

.title {
  display: inline; }

.icon {
  margin-right: 0.8rem; }

.body {
  flex: 1 1 auto;
  padding: 0.8rem 0.8rem 0;
  font-size: 2rem;
  overflow: auto;
  position: relative; }
  .body:after {
    content: "";
    height: 0.8rem;
    display: block;
    visibility: hidden; }

.closeBtn {
  position: absolute;
  z-index: 1;
  display: block;
  top: 0.6rem;
  right: 0.7rem;
  vertical-align: top;
  line-height: 0.9;
  padding: 0;
  border: none;
  margin: 0;
  background: transparent;
  font-size: 2.5rem;
  outline: 0 !important; }
  .closeBtn:hover {
    color: #7F7F7F;
    cursor: pointer; }
  :global(.focus_keyboard) .closeBtn:focus {
    color: #EEBB11; }
  .closeBtn > * {
    pointer-events: none; }

.overlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); }

.panel {
  background: #000;
  flex-grow: 1;
  padding: 0.8rem; }

.noPad {
  padding: 0; }
