.systemMapWrapper {
  height: 100%; }

.systemMap {
  display: block;
  width: 100%;
  height: 100%;
  background: transparent url(../../../assets/images/bg.png) repeat;
  outline: 0 !important;
  overflow: hidden; }
  .systemMap:focus {
    outline: 0 !important; }

.systemBody.star {
  fill: #FDFF00; }

.systemBody.gasGiant {
  fill: #6666CC; }

.systemBody.planet {
  fill: #3333FF; }

.systemBody.dwarfPlanet {
  fill: #4444FF; }

.systemBody.moon {
  fill: #4444FF; }

.systemBody.asteroid {
  fill: #999; }

.systemBodyLabel {
  fill: #FFF;
  font: normal 20px sans-serif;
  text-anchor: middle;
  pointer-events: none;
  dominant-baseline: hanging; }

.orbit {
  stroke: #FFF;
  stroke-width: 1px;
  stroke-opacity: 0.25;
  fill: none;
  pointer-events: none; }

.colonyHighlight {
  stroke: #3D3;
  stroke-width: 1.5px;
  fill: none; }
